@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    font: 400 16px 'Open Sans', sans-serif;
    color:aliceblue;
}

.app{
    text-align:center;
}

.menu-guia{
    justify-content: right;
    padding-right: 7%;
}

.app-footer{
    width: 100%;
    height: 100px;
    background-color: rgb(0, 0, 0);
}

.footer_copyright{
    bottom: 8px;
    width: 100%;
    text-align: center;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
}

.footer_social {
    width: 100%;
    padding-top: 20px;
    display: flex;
    justify-content: center;
}

.link{
  color: white;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  text-decoration: none;
}

.text{
  color: aliceblue;
}

.text-title{
  color: #EBE44B;
  font-family: 'Open Sans', sans-serif;

  font-style: normal;
  font-weight: 600;
  font-size: 80px;
  /* text-transform: capitalize; */
  text-align:  center;
  /* text-shadow: 1px 1px 1px #000, -1px 1px 1px #000,1px -1px 1px #000,-1px -1px 1px #000; */
  padding-bottom: 50px;
  padding-top: 50px;

  color:white;
  font-size: 4rem;
  -webkit-text-fill-color: #EBE44B;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;


}

.text-page{
  color: #000;
  font-family: Open Sans;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-align: justify;
  display: flex;

  align-content: center;
}

.tela{
  min-height: 80vh;
}

.img-responsive{
  height:80vh;
}

.app-congonhas{
    margin-top: 100px;
    margin-bottom: 100px;
    margin-left: 100px;
    margin-right: 100px;
    padding-top: 100px;
    padding-left: 100px;
    padding-right: 100px;
    padding-bottom: 100px;
    color:black;
}
.container-page{
    margin-top: 3rem;
    margin-bottom: 3rem;
    margin-left: 7rem;
    margin-right: 7rem;
    /* min-width: 70rem; */
    background-color: #f7f2ff; /* Cor da caixa de fora */
}

.titulopag{
    color: #EBE44B;
    font-family: 'Open Sans', sans-serif;
    
    font-style: normal;
    font-weight: 600;
    font-size: 80px;
    /* text-transform: capitalize; */
    text-align:  center;
    /* text-shadow: 1px 1px 1px #000, -1px 1px 1px #000,1px -1px 1px #000,-1px -1px 1px #000; */
    padding-bottom: 50px;
    padding-top: 50px;

    color:white;
    font-size: 4rem;
    -webkit-text-fill-color: #EBE44B;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;

    
}

.subtitulo{
    color: #EBE44B;
    font-family: Open Sans;
    font-weight: 600;
    font-size: 2rem;
    text-transform: capitalize;
    text-shadow: 1px 1px 1px #000, -1px 1px 1px #000,1px -1px 1px #000,-1px -1px 1px #000;
    padding-left: 9rem;
}

.title {
    color: #EBE44B;
    font-family: Open Sans;
    font-weight: 200;
    font-size: 2rem;
    text-transform: capitalize;
    text-shadow: 1px 1px 1px #000, -1px 1px 1px #000,1px -1px 1px #000,-1px -1px 1px #000;
    padding-left: 180px;
  }
  
  .title-alt{
    border-bottom: 1px solid white;
    margin-bottom: 2rem;
    padding: 1rem;
    margin: 0.5rem;
    height: var(--header-height);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black; /* Cor da caixa do subtitulo */
    border-radius: 0.25rem;
  }

.paragrafo1{
    color: #000;
    font-family: Open Sans;
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: justify;
    display: flex;
    min-width: 400px;
    max-width: 600px;
    align-content: center;
}

.paragrafo2{
    color: #000;
    font-family: Open Sans;
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: justify;
    display: flex;
    min-width: 400px;
    max-width: 600px;
}

.tabela{
    padding-top: 3rem;
    padding-left: 3rem;
    padding-right: 3rem;
    padding-bottom: 3rem;
    display: flex;
    align-self: center;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.imagens{
    object-fit: cover;
}


